import React, { useState } from "react";
import AppHeader from "../AppHeader/AppHeader";
import Main from "../Main/main";
import logo from "../../img/woodec-logo.svg";
import unionBottom from "../../img/UnionBottom.svg";

import Data from "../../data.json";
import "./App.css";

const App = () => {
  const defaultLang = () => {
    switch (navigator.language) {
      case "uk":
        return "ua", localStorage.setItem("lang", "ua");
      case "en-US":
        return "en", localStorage.setItem("lang", "en");
      case "en":
        return "en", localStorage.setItem("lang", "en");
      default:
        return "pl", localStorage.setItem("lang", "pl");
    }
  };
  const setLanguage = (language) => {
    if (language === undefined) {
      return localStorage.getItem("lang");
    }
    if (localStorage.getItem("lang") === null) {
      return localStorage.setItem("lang", navigator.language);
    } else {
      return localStorage.setItem("lang", language);
    }
  };

  const [lang, setLang] = useState(
    localStorage.getItem("lang") === null ? defaultLang() : setLanguage()
  );
  console.log("lang", lang);
  const [activeMenuItem, setActiveMenuItem] = useState("/");
  const [expandedAccordion, setExpandedAccordion] = useState("");
  const langs = Object.keys(Data);
  const data = Data[lang];
  return (
    <>
      <div className="container">
        <AppHeader
          lang={lang}
          langs={langs}
          data={data.menu}
          setLang={setLang}
          activeMenuItem={activeMenuItem}
          setActiveMenuItem={setActiveMenuItem}
          setExpandedAccordion={setExpandedAccordion}
          setLanguage={setLanguage}
        />
        <Main
          lang={lang}
          data={data}
          activeMenuItem={activeMenuItem}
          setActiveMenuItem={setActiveMenuItem}
          expandedAccordion={expandedAccordion}
          setExpandedAccordion={setExpandedAccordion}
        />
      </div>
      <footer className="footerApp">
        <img className="unionBottom" src={unionBottom} />
      </footer>
    </>
  );
};

export default App;
