import React from "react";
import { ReactComponent as IconMarker } from "../../img/iconMap.svg";
import GoogleMapReact from "google-map-react";
import "./Address.css";

const Address = (props) => {
  const address = props.address;
  const defaultProps = {
    center: {
      lat: 50.3432,
      lng: 18.15658,
    },
    zoom: 17,
  };

  return (
    <div className="containerMap">
      <div className="googleContainer">
        <GoogleMapReact
          bootstrapURLKeys={{
            key: process.env.REACT_APP_GOOGLE_KEY,
            language: props.lang,
          }}
          defaultCenter={defaultProps.center}
          defaultZoom={defaultProps.zoom}
        >
          <IconMarker
            lat={50.3432}
            lng={18.15658}
            text="My Marker"
            className="marker"
          />
        </GoogleMapReact>
      </div>
      <div className="containerAddress">
        <h2 className="address">{address.fieldAddress}:</h2>
        <p className="address">{address.country}</p>
        <p className="address">
          {address.city}, {address.street}
        </p>
        <p className="address">
          {address.fieldPhone}:{" "}
          <a href="tel:{address.phone}">{address.phone}</a>
        </p>
        <p className="address">
          {address.fieldEmail}: <a href="mailto:">{address.email}</a>
        </p>
      </div>
    </div>
  );
};

export default Address;
