import React, { useRef, useState } from "react";
import emailjs from "@emailjs/browser";
import { Formik, Field } from "formik";
import "./ContactForm.css";

const ContactForm = (props) => {
  const formData = props.form;
  const [activeInput, setActiveInput] = useState(false);
  const form = useRef();
  const handleFocus = () => {
    setActiveInput(true);
  };
  const handleBlur = () => {
    setActiveInput(false);
  };
  const sendEmail = (e) => {
    e.preventDefault();

    emailjs
      .sendForm(
        "service_0anaq2m",
        "template_9i7xmzv",
        form.current,
        "N5zYaee7UCsoouJ2V"
      )
      .then(
        (result) => {
          console.log(result.text);
        },
        (error) => {
          console.log(error.text);
        }
      );
  };
  return (
    <>
      <form className="containerContactForm" ref={form} onSubmit={sendEmail}>
        <div className="containerFieldEmail">
          <label className="fieldEmailLabel">{formData.email}</label>
          <input
            onFocus={(e) => {
              e.target.classList.add("activeField");
            }}
            onBlur={(e) => {
              e.target.classList.remove("activeField");
            }}
            className={`fieldEmailInput`}
            label={formData.email}
            name="user_email"
          />
        </div>
        <div className="containerFieldName">
          <div className="fieldName">
            <label className="fieldNameLabel">{formData.name}</label>
            <input
              onFocus={(e) => {
                e.target.classList.add("activeField");
              }}
              onBlur={(e) => {
                e.target.classList.remove("activeField");
              }}
              className={`fieldNameInput`}
              label={formData.name}
              name="user_name"
            />
          </div>
          <div className="fieldName">
            <label className="fieldNameLabel">{formData.lastName}</label>
            <input
              onFocus={(e) => {
                e.target.classList.add("activeField");
              }}
              onBlur={(e) => {
                e.target.classList.remove("activeField");
              }}
              className={`fieldNameInput`}
              label={formData.lastName}
              name="user_lastName"
            />
          </div>
        </div>
        <div className="containerFieldMessage">
          <label className="fieldMessageLabel">{formData.message}</label>
          <input
            onFocus={(e) => {
              e.target.classList.add("activeField");
            }}
            onBlur={(e) => {
              e.target.classList.remove("activeField");
            }}
            className={`fieldMessageInput`}
            label={formData.message}
            name="message"
            fullWidth
            multiline
            rows={5}
          />
        </div>
        <button className="buttonFormSubmit" type="submit">
          {formData.submit}
        </button>
        {/*  <label>Name</label>
        <input type="text" name="user_name" />
        <label>Email</label>
        <input type="email" name="user_email" />
        <label>Message</label>
        <textarea name="message" />
        <input type="submit" value="Send" />*/}
      </form>
    </>
  );
};
export default ContactForm;
